<template>
  <div class="v-accordion">
    <list class="list-accordion list-borderless" v-for="(it, index) in items" :key="index">
      <item class="item-icon-right" @click.native="onClick(index)">
        <span v-text="it.title"></span>
        <i class="icon ion-arrow-down-b"
          :class="{'ion-arrow-up-b': active == index}"></i>
      </item>

      <item class="item-accordion-content" :class="{'show': active == index}">
        <div v-html="it.content"></div>
      </item>
    </list>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../scss/mixins';

  .v-accordion {
    padding: 0 0 0 20px;
    background: #FFF;

    .list-accordion {
      margin-top: -1px;
      margin-bottom: 0px;

      .item {
        font-size: 16px;
        color: #3A3A3A;
        padding: 14px 0;

        @include font-regular();
      }

      .item-icon-right {
        .icon.ion-arrow-down-b {
          position: relative;
          float: right;
          right: 20px;
          width: 12px;
          height: 10px;
          margin-top: 5px;

          &::before {
            content: ' ';
            width: 10px;
            height: 2px;
            background: #DDDDDD;
          }

          &::after {
            content: ' ';
            width: 10px;
            height: 2px;
            background: #DDDDDD;
            display: block;
            position: absolute;
            right: 2px;
            top: 4px;
            transform: rotateZ(90deg);
            -ms-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -webkit-transform: rotate(90deg);
            -o-transform: rotate(90deg);
          }

          &.ion-arrow-up-b {
            transform: scaleY(-1);
          }

          &.ion-arrow-up-b::after {
            display: none;
          }
        }
      }

      .item-accordion-content {
        min-height: 0px;
        white-space: normal;
        font-size: 13px;
        line-height: 22px;
        color: #888888;
        letter-spacing: 1px;
        text-align: justify;
        transition: 0.2s cubic-bezier(0.4, 0.6, 0.2, 1);
        -webkit-transition: 0.2s cubic-bezier(0.4, 0.6, 0.2, 1);
        height: 0px;
        padding: 0px 35px 0px 0;

        &.show {
          height: auto;
          padding: 4px 35px 20px 0;
        }
      }
    }
  }
</style>
<script>
  export default {
    props: {
      activeIndex: {
        type: Number,
        require: true
      },
      items: {
        type: Array,
        require: true
      }
    },

    data () {
      return {
        active: this.activeIndex
      }
    },

    methods: {
      onClick (index) {
        this.active = (this.active === index ? -1 : index)
      }
    }
  }
</script>



