<template>
  <div class="page">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="帮助中心"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content help">
      <div class="row row-choice">
        <div class="col" @click="$router.push('./help/self')"><i class="i-self"></i><span>自助服务</span></div>
        <a class="col c3a" :href="curstomerServiceLink"><div><i class="i-manual"></i><span>人工服务</span></div></a>
      </div>
      <div class="common-question-title">常见问题</div>
      <div>
        <!-- 常见问题 -->
        <accordion :items="questions" :active-index="activeIndex"></accordion>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scope>
  @import "../../../scss/mixins";

  .help {
    padding: 50px 0 0;
    background-color: #fff !important;

    @include font-regular();

    .row {
      text-align: center;
    }

    .row-choice {
      font-size: 14px;
      color: #3A3A3A;
      margin-top: 20px;

      .col {
        @include font-semibold()
      }
    }

    .c3a {
      color: #3A3A3A;
      text-decoration: none;
    }

    .i-self {
      display: block;
      width: 38px;
      height: 38px;
      margin: 0 auto 5px;
      background: url("//qianka.b0.upaiyun.com/images/22076f215a857d8b5c0dd47562a39e1e.png") no-repeat;
      background-size: 100% 100%;
    }

    .i-manual {
      display: block;
      width: 38px;
      height: 38px;
      margin: 0 auto 5px;
      background: url("//qianka.b0.upaiyun.com/images/d2a75aeb0a52def599665e6e5c74f716.png") no-repeat;
      background-size: 100% 100%;
    }

    .common-question-title {
      height: 36px;
      line-height: 36px;
      font-size: 20px;
      color: #DDDDDD;
      letter-spacing: 0;
      margin: 20px 0 0;
      padding-left: 20px;
    }

    .hairline-top::before {
      visibility: hidden;
    }

    .hairline-bottom::after {
      visibility: hidden;
    }

    .v-accordion .list-accordion .item {
      line-height: 22px;
    }
  }

</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  import Accordion from '@/widget/Accordion.vue'
  import {QUESTIONS} from './constants'
  import { getCustomerServiceLink } from 'utils'

  export default {
    components: {
      AnimateHeader,
      Accordion
    },

    data () {
      return {
        questions: QUESTIONS,
        questionIndex: 0,
        activeIndex: -1,
        curstomerServiceLink: null
      }
    },

    created () {
      this.curstomerServiceLink = getCustomerServiceLink()
    },

    methods: {
      back () {
        $router.back('/user')
      }
    }
  }
</script>



